import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { PlayerRoutingModule } from './player-routing.module';
import { IntroComponent } from './intro/intro.component';
import { PlayerComponent } from './player.component';
import { BasicMovesComponent } from './basic-moves/basic-moves.component';
import { SharedModule } from '../shared/shared.module';
import { SpecialMovesComponent } from './special-moves/special-moves.component';
import { FighterComponent } from './classes/fighter/fighter.component';
import { ClassesComponent } from './classes/classes.component';
import { HeritagesComponent } from './heritages/heritages.component';
import { EquipmentComponent } from './equipment/equipment.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    PlayerRoutingModule
  ],
  declarations: [
    PlayerComponent,
    IntroComponent,
    BasicMovesComponent,
    SpecialMovesComponent,
    FighterComponent,
    ClassesComponent,
    HeritagesComponent,
    EquipmentComponent
  ]
})
export class PlayerModule { }



