import { Cachable } from "./ObjectCache";
import { CharacterHeritage } from "./CharacterHeritage";
import { Move } from "./Move";
import { GearStack } from "./Gear";

export interface AttributeHolder {
  STR: number;
  DEX: number;
  CON: number;
  INT: number;
  WIS: number;
  CHA: number;
}

export abstract class CharacterClass implements Cachable, AttributeHolder {
  static factory(): CharacterClass { return null; }

  _id: number;
  slug: string;
  className: string;
  name: string;
  aspects: CharacterAspect[];
  heritage: CharacterHeritage;
  heritageHold: number;
  drives: string[];
  load: number;
  damageDie: number; // Classic poly dice
  damageMod: number; // Alternative modified d6
  maximumHP: number;
  currunteHP: number;
  level: number;
  XP: number;
  STR: number;
  DEX: number;
  CON: number;
  INT: number;
  WIS: number;
  CHA: number;
  debilities: CharacterDebility[];
  startingMoves: StartingMoveBlock[];
  startingGear: StartingGearBlock[];
  armor: number;
  bonds: string[];
  advancedMoves: Move[];
  availableMoves: CharacterMove[];

  notes: { time: Date; value: string; }[];
  avatar: { url: string; crop: { x: number; y: number; width: number; height: number; } };
}

export interface CharacterAspect {
  subject: string;
  adjective: string;
}
export interface StartingMoveBlock { }
export class StartingMove implements StartingMoveBlock {
  move: Move;
}
export class StartingMoveSelection implements StartingMoveBlock {
  title: string;
  arity: number;
  options: { selected: boolean; move: Move; }[];
}

export interface StartingGearBlock { }
export class StartingGear implements StartingGearBlock {
  items: GearStack[];
}
export class StartingGearSelection implements StartingGearBlock {
  title: string;
  arity: number;
  options: { selected: boolean; items: GearStack[]; }[]
}

export interface CharacterDebility {
  attribute: keyof AttributeHolder;
  description: string;
  duration: string;
}

export interface CharacterMove {
  move: Move;
  availableHold: number;
}



