import { Injectable } from '@angular/core';
import { TagDataService } from '../data/tag-data.service';
import { MoveDataService } from '../data/move-data.service';

@Injectable()
export class InitSpecialMovesService {

  constructor(
    private tagData: TagDataService,
    private moveData: MoveDataService
  ) { }

  initData() {

    var specialMoveTag = this.tagData.findOneByName("TAG.SPECIALMOVE");
    var playerMoveTag = this.tagData.findOneByName("TAG.PLAYERMOVE");

    this.moveData.insert({
      tags: [playerMoveTag, specialMoveTag],
      slug: "last_breath",
      i18nPrefix: "MOVE.LAST_BREATH.",
      name: "NAME",
      trigger: "TRIGGER",
      rollModifier: "CHARACTER.ATTRIBUTE.CON",
      effect: "EFFECT",
      rollResultBreakdown: [
        {range: "10+", effect: "RES.10+"},
        {range: "7-9", effect: "RES.7-9"},
        {range: "6-", effect: "RES.6-"}
      ],
      comments: [
      ]
    });

    this.moveData.insert({
      tags: [playerMoveTag, specialMoveTag],
      slug: "encumbrance",
      i18nPrefix: "MOVE.ENCUMBRANCE.",
      name: "NAME",
      trigger: "TRIGGER",
      effect: "EFFECT",
      details: "DETAILS",
      rollResultBreakdown: [],
      comments: []
    });

    this.moveData.insert({
      tags: [playerMoveTag, specialMoveTag],
      slug: "make_camp",
      i18nPrefix: "MOVE.MAKE_CAMP.",
      name: "NAME",
      trigger: "TRIGGER",
      effect: "EFFECT",
      rollResultBreakdown: [],
      comments: [
        "COMMENT.1",
        "COMMENT.2"
      ]
    });

    this.moveData.insert({
      tags: [playerMoveTag, specialMoveTag],
      slug: "take_watch",
      i18nPrefix: "MOVE.TAKE_WATCH.",
      name: "NAME",
      trigger: "TRIGGER",
      rollModifier: "CHARACTER.ATTRIBUTE.WIS",
      rollResultBreakdown: [
        {range: "10+", effect: "RES.10+"},
        {range: "7-9", effect: "RES.7-9"},
        {range: "6-", effect: "RES.6-"}
      ],
      comments: [
      ]
    });

    this.moveData.insert({
      tags: [playerMoveTag, specialMoveTag],
      slug: "UAPJ",
      i18nPrefix: "MOVE.UAPJ.",
      name: "NAME",
      trigger: "TRIGGER",
      effect: "EFFECT",
      rollModifier: "CHARACTER.ATTRIBUTE.WIS",
      rollResultBreakdown: [
        {range: "10+", effect: "RES.10+"},
        {range: "7-9", effect: "RES.7-9"}
      ],
      comments: [
        "COMMENT.1",
        "COMMENT.2",
        "COMMENT.3"
      ]
    });

    this.moveData.insert({
      tags: [playerMoveTag, specialMoveTag],
      slug: "CAROUSE",
      i18nPrefix: "MOVE.CAROUSE.",
      name: "NAME",
      trigger: "TRIGGER",
      effect: "EFFECT",
      rollModifier: "MOVE.CAROUSE.ROLL_MODIFIER",
      rollResultBreakdown: [
        {range: "10+", effect: "RES.10+"},
        {range: "7-9", effect: "RES.7-9"},
        {range: "6-", effect: "RES.6-"}
      ],
      details: "DETAILS",
      comments: [
        "COMMENT.1",
        "COMMENT.2",
        "COMMENT.3"
      ]
    });

    this.moveData.insert({
      tags: [playerMoveTag, specialMoveTag],
      slug: "SUPPLY",
      i18nPrefix: "MOVE.SUPPLY.",
name: "NAME",
      trigger: "TRIGGER",
      rollModifier: "CHARACTER.ATTRIBUTE.CHA",
      rollResultBreakdown: [
        {range: "10+", effect: "RES.10+"},
        {range: "7-9", effect: "RES.7-9"}
      ],
      details: "DETAILS"
    });

    this.moveData.insert({
      tags: [playerMoveTag, specialMoveTag],
      slug: "RECOVER",
      i18nPrefix: "MOVE.RECOVER.",
name: "NAME",
      trigger: "TRIGGER",
      effect: "EFFECT"
    });

    this.moveData.insert({
      tags: [playerMoveTag, specialMoveTag],
      slug: "BOLSTER",
      i18nPrefix: "MOVE.BOLSTER.",
name: "NAME",
      trigger: "TRIGGER",
      effect: "EFFECT",
      details: "DETAILS"
    });

    this.moveData.insert({
      tags: [playerMoveTag, specialMoveTag],
      slug: "RECRUIT",
      i18nPrefix: "MOVE.RECRUIT.",
name: "NAME",
      trigger: "TRIGGER",
      effect: "EFFECT",
      rollResultBreakdown: [
        {range: "10+", effect: "RES.10+"},
        {range: "7-9", effect: "RES.7-9"},
        {range: "6-", effect: "RES.6-"}
      ],
      details: "DETAILS",
      comments: [
      ]
    });

    this.moveData.insert({
      tags: [playerMoveTag, specialMoveTag],
      slug: "OUTSTANDING_WARRANTS",
      i18nPrefix: "MOVE.OUTSTANDING_WARRANTS.",
name: "NAME",
      trigger: "TRIGGER",
      rollResultBreakdown: [
        {range: "10+", effect: "RES.10+"},
        {range: "7-9", effect: "RES.7-9"}
      ],
      details: "DETAILS",
      comments: [
      ]
    });

  }
}




