import { Injectable } from '@angular/core';
import { Fighter } from '../data/FighterClass';
import { TagDataService } from '../data/tag-data.service';
import { MoveDataService } from '../data/move-data.service';
import { HeritageDataService } from '../data/heritage-data.service';
import { GearDataService } from '../data/gear-data.service';
import { StartingGearBlock, StartingGearSelection } from '../data/CharacterClass';

@Injectable()
export class InitFighterClassService {

  constructor(
    private tagData: TagDataService,
    private moveData: MoveDataService,
    private gearData: GearDataService,
  ) { }

  initData() {

    var playerMoveTag = this.tagData.findOneByName("TAG.PLAYERMOVE");
    var fighterStartingMoveTag = this.tagData.findOneByName("TAG.FIGHTER_STARTING");
    var fighterAdvancedMoveTag = this.tagData.findOneByName("TAG.FIGHTER_ADVANCED");

    Fighter.factoryMethod = () => {
      let f = new Fighter();

      f.startingGear.push({
        title: "choose",
        arity: 1,
        options:[
          {
            selected: false,
            items: [
              { item: this.gearData.findOneBySlug("armor_chain"), size: 1 },
              { item: this.gearData.findOneBySlug("shield"), size: 2 }
            ]
          },
          {
            selected: false,
            items: [
              { item: this.gearData.findOneBySlug("armor_plate"), size: 1 }
            ]
          }
        ]
      });
      f.startingGear.push({
        items: [
          { item: this.gearData.findOneBySlug("armor_leather"), size: 1 },
          { item: this.gearData.findOneBySlug("armor_chain"), size: 1 },
          { item: this.gearData.findOneBySlug("armor_scale"), size: 1 }
        ]
      });

      f.startingMoves.push({
        move: this.moveData.insert({
          slug: "bend_bars_lift_gates",
          tags: [playerMoveTag, fighterStartingMoveTag],
          i18nPrefix: "MOVE.CLASS.FIGHTER.STARTING.BBLG.",
          name: "NAME",
          trigger: "TRIGGER",
          rollModifier: "CHARACTER.ATTRIBUTE.STR",
          rollResultBreakdown: [
            { range: "10+", effect: "RES.10+" },
            { range: "7-9", effect: "RES.7-9" }
          ],
          details: "DETAILS"
        })
      });

      f.startingMoves.push({
        move: this.moveData.insert({
          slug: "armored",
          tags: [playerMoveTag, fighterStartingMoveTag],
          i18nPrefix: "MOVE.CLASS.FIGHTER.STARTING.ARMORED.",
          name: "NAME",
          trigger: "TRIGGER",
          effect: "EFFECT"
        })
      });

      f.startingMoves.push({
        move: this.moveData.insert({
          slug: "signature_weapon",
          tags: [playerMoveTag, fighterStartingMoveTag],
          i18nPrefix: "MOVE.CLASS.FIGHTER.STARTING.SIGNATURE_WEAPON.",
          name: "NAME",
          effect: "EFFECT",
          details: "DETAILS"
        })
      });


      return f;
    };

    console.debug("Fighter Class initialized");
  }
}



