import { CharacterClass, CharacterAspect, StartingMoveBlock, StartingGearBlock } from './CharacterClass';

export class Fighter extends CharacterClass {
  constructor() {
    super();

    this.slug = "fighter";
    this.className = "CHARACTER.CLASS.FIGHTER";
    this.aspects = [
      { subject: "CHARACTER.ASPECT.EYES", adjective: "" },
      { subject: "CHARACTER.ASPECT.HAIR", adjective: "" },
      { subject: "CHARACTER.ASPECT.SKIN", adjective: "" },
      { subject: "CHARACTER.ASPECT.BODY", adjective: "" }
    ];
    this.load = 12;
    this.currunteHP = this.maximumHP = 24;
    this.damageDie = 10;
    this.damageMod = +2;
    this.startingMoves = [];
    this.advancedMoves = [];
    this.startingGear = [];
    this.XP = 0;
    this.level = 1;
    this.STR = 0;
    this.DEX = 0;
    this.CON = 0;
    this.INT = 0;
    this.WIS = 0;
    this.CHA = 0;
    this.armor = 0;
    this.availableMoves = [];
  }

  static factoryMethod: () => Fighter;

  static factory(): Fighter {
    return Fighter.factoryMethod();
  }
}



