import { Injectable } from '@angular/core';

@Injectable()
export class RandomTablesService {
  public static CMD_REGEX = /\{\s*(\#)?\s*([^\}]+)\s*\}/g;
  public static ASSIGN_REGEX = /(\$[a-z][a-z0-9_]*)\s*=\s*([^=].*)/;
  public static PIPE_REGEX = /(\'[^']*\'|\$[a-z][a-z0-9_]*|\[[^:]*:[^\]]+\])\|(C)/g;
  public static VAR_REGEX = /(\$[a-z][a-z0-9_]*)/g;
  public static COLLDEF_REGEX = /\[([0-9\.\,]+)\]/g;
  public static RANGE_REGEX = /^([0-9]+)\.\.([0-9]+)$/;
  public static LIST_REGEX = /^([0-9]+(,[0-9]+)+)$/;
  public static ROLL_REGEX = /([0-9]*)d([0-9]+|\$[0-9a-z_]+)(\!)?([BW<>][1-9]+)?([L])?/g;
  public static TABLE_LOOKUP_REGEX = /^\s*\[(\'[^\']*\')?([^\:\']+)?\:([A-Z_\.]+)\]\s*$/;

  static RED_BEST_REGEX = /^B([0-9]+)$/;
  static RED_WORST_REGEX = /^W([0-9]+)$/;
  static RED_SUCC_REGEX = /^([<>][0-9]+)$/;

  constructor() { }

  static eval(sentence: string, context): string {
    if (context === undefined) {
      context = {};
    }
    if (context['@listIndex'] === undefined) {
      context['@listIndex'] = 1;
    }
    if (context['@tables'] === undefined) {
      context['@tables'] = {};
    }

    //console.log("------------- BEGIN -------------")
    if (this.CMD_REGEX.test(sentence)) {

      var sentence = sentence.replace(this.CMD_REGEX, (sub, silent, cmd) => {
        var replace = this.execCmd(cmd, context);
        return silent ? "" : replace;
      });

    }
    //console.log("------------- END -------------")
    return sentence;
  }

  static execCmd(cmd: string, context): string {
    //console.log("Command", cmd)

    if (this.ASSIGN_REGEX.test(cmd)) {
      //console.log("Assign command...")
      var a = this.ASSIGN_REGEX.exec(cmd);
      var value = this.execCmd(a[2], context);
      //console.log("Assign ", a[1], " = ", value);
      return context[a[1]] = value;

    } else {

      // Catch Pipe functions
      cmd = cmd.replace(this.PIPE_REGEX, (sub: string, piped: string, pipe: string) => {
        //console.log(sub, piped, pipe);
        var transformed = this.execCmd(piped, context);
        switch (pipe) {
          case "C":
            transformed = this.capitalize(transformed);
            break;
        }
        return this.asStringValue(transformed);
      });

      // Replace all variables with values
      cmd = cmd.replace(this.VAR_REGEX, (sub, varName, idx) => {

        var value = context[varName];

        if (value === undefined) {
          value = sub;
        } else if (typeof value === "string") {
          value = this.asStringValue(value);
        } else if (typeof value === "number") {
          value = "" + value;
        } else if (value.length > 0) {
          if (idx > 0 && cmd.charAt(idx - 1) == "d") {
            // It's a collection meant for a roll
            //console.log("collection variabile for roll")
            value = sub;
          } else {
            value = this.asStringValue(value);
          }
        }

        //console.log("Variable", sub, varName, value);
        return value;
      });

      // Resolve collections
      cmd = cmd.replace(this.COLLDEF_REGEX, (sub: string, listDef: string) => {
        //console.log("Collection", sub);
        return this.resolveCollection(listDef, context);
      });

      // Replace all rolls with values
      cmd = cmd.replace(this.ROLL_REGEX, (sub: string, samples, collection, explode, reduction, asList): string => {
        //console.log("Roll", sub, samples, collection, explode, reduction, asList);
        return "" + this.roll(parseInt(samples), collection, explode !== undefined, reduction, asList !== undefined, context);
      });

      // Lookup tables
      cmd = cmd.replace(this.TABLE_LOOKUP_REGEX, (sub: string, tags: string, lookup: string, tableName: string) => {
        //console.log("Table lookup", sub, tableName, tags, lookup);
        var lookupId = lookup === undefined ? null : parseInt(this.execCmd(lookup, context));
        if (tags) tags = tags.replace(/(^\s*'|'\s*$)/g, "")
        var evaluatedLookup = this.tableLookup(tableName, tags, lookupId, context);
        return this.asStringValue(evaluatedLookup);
      });

      // See if it's just a value!
      return this.evalInContext(cmd, context);
    }
  }

  static _deepGet(p, o) {
    return p.reduce((xs, x) => (xs && xs[x]) ? xs[x] : null, o);
  }


  static tableLookup(tableName: string, tagExpression: string, id: number, context): string {
    var tablePath = tableName.split(".");
    var table = this._deepGet(tablePath, context['@tables']);
    console.log("Access table: ", tableName, tablePath, table, tagExpression, id, context);
    if (tagExpression) {

      table = table.filter((entry) => {

        var tagContext = {};
        var entryTags = entry[0];

        tagExpression.split(/\s*(?:\&\&|\|\||[\(\)\!\'])\s*/g).filter(tagName => tagName.length).map((tagName) => {
          tagContext[tagName] = false;
          return tagName;
        });
        entryTags.split(" ").filter(tagName => tagName.length).map(tagName => {
          tagContext[tagName] = true;
        });

        var evaluation = this.evalInContext(tagExpression, tagContext);

        //console.log("EVALUATING ENTRY ", entry, tagExpression, evaluation)

        return evaluation;
      });
    }
    if (table === undefined || table === null) {
      return "unknown table";
    } else {
      var lastId = table.length - 1;
      id = id !== null ? Math.min(id, lastId) : this.randInt(0, lastId);
      var tableSample = table[id];
      //console.log("Table sample id: ", id, tableSample);
      var resolvedTableSample;

      if (tablePath.length > 1) {
        //console.log("Performing Tables Switcheroo", context['@tables'][tablePath[tablePath.length - 2]])
        var currTables = context['@tables'];
        context['@tables'] = context['@tables'][tablePath[tablePath.length - 2]];
        resolvedTableSample = this.eval(tableSample[1], context);
        context['@tables'] = currTables;
      } else {
        resolvedTableSample = this.eval(tableSample[1], context);
      }
      return resolvedTableSample;
    }
  }

  static resolveCollection(listDef, context) {
    var listIndex = '$' + context['@listIndex']++;

    if (this.LIST_REGEX.test(listDef)) {

      context[listIndex] = listDef.split(",").map((item) => parseInt(item));

    } else if (this.RANGE_REGEX.test(listDef)) {

      var rangeDef = this.RANGE_REGEX.exec(listDef); //console.log(rangeDef)
      var range = [];
      var min = parseInt(rangeDef[1]);
      var max = parseInt(rangeDef[2]);
      for (var i = min; i <= max; i++) range.push(i);
      context[listIndex] = range;

    }

    return listIndex;
  }

  static roll(samples: number, collection: string, explode: boolean, reduction: string, asList: boolean, context) {

    var allRolls: Array<number>;
    if (collection.charAt(0) == '$') {
      allRolls = this.rollCollection(samples, context[collection], explode);
    } else {
      allRolls = this.rollDie(samples, parseInt(collection), explode);
    }

    if (reduction) {
      var target = parseInt(reduction.substring(1));
      if (this.RED_BEST_REGEX.test(reduction)) {
        allRolls = this.reduceBestSubset(allRolls, target);
      } else if (this.RED_WORST_REGEX.test(reduction)) {
        allRolls = this.reduceWorstSubset(allRolls, target);
      } else if (this.RED_SUCC_REGEX.test(reduction)) {
        allRolls = this.reduceSuccesses(allRolls, reduction, target);
      }
    }

    if (asList) {
      var listIndex = '$' + context['@listIndex']++;
      context[listIndex] = allRolls;
      return listIndex;
    } else {
      return allRolls.reduce((prev, curr) => {
        return prev + curr;
      }, 0);
    }
  }

  static rollDie(samples: number, dieType: number, explode): Array<number> {
    var n = samples;
    var rolledSamples = [];
    while (n > 0) {
      var sample = this.randInt(1, dieType);
      rolledSamples.push(sample);
      if (!explode || sample != dieType) {
        n--;
      }
    }
    return rolledSamples;
  }

  static rollCollection(samples: number, collection: Array<number>, explode): Array<number> {
    var n = samples;
    var rolledSamples = [];
    while (n > 0) {
      var roll = this.randInt(0, collection.length - 1);
      var sample = collection[roll];
      rolledSamples.push(sample);
      if (!explode || roll != collection.length - 1) {
        n--;
      }
    }
    return rolledSamples;
  }

  static reduceBestSubset(rolledSamples: Array<number>, keep: number): Array<number> {
    rolledSamples.sort().splice(0, rolledSamples.length - keep);
    return rolledSamples;
  }

  static reduceWorstSubset(rolledSamples: Array<number>, keep: number): Array<number> {
    rolledSamples.sort().reverse().splice(0, rolledSamples.length - keep);
    return rolledSamples;
  }

  static reduceSuccesses(rolledSamples: Array<number>, successCondition: string, target: number): Array<number> {
    if (successCondition.charAt(0) == ">") {
      return rolledSamples.filter((s) => {
        return s > target;
      });
    } else if (successCondition.charAt(0) == "<") {
      return rolledSamples.filter((s) => {
        return s < target;
      });
    }
  }

  static asStringValue(value): string {
    return ["'", value.replace("'", "\\'"), "'"].join("");
  }

  static randInt(min, max): number {
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

  static evalInContext(command, context) {
    // execute script in private context
    // console.log("EVAL", command, context);
    var src = command.replace(this.VAR_REGEX, (sub, varname) => {
      if (context[varname] === undefined) {
        context[varname] = undefined;
      }
      return sub;
    });
    var r;
    try {
      r = (new Function("with(this) { return " + command + "}")).call(context);
    } catch (e) {
      console.log("Was evaluating '"+command+"' in", context);
      console.error(e);
    }
    return r;
  }


  static capitalize(line: string): string {
    if (typeof line === "string" && line.length > 0) {
      return line.charAt(0).toUpperCase() + line.substring(1);
    } else {
      return line;
    }
  }
}



