import { Component, OnInit } from '@angular/core';
import { TestGenTables } from './TestGenTables.class';
import { Gevaluator2Service } from '../gevaluator2.service';
import { BasicTables } from '../generators/tables/BasicTables.class';
import { RandomTablesService } from '../generators/tables/random-tables.service';

@Component({
  selector: 'app-test-generator',
  templateUrl: './test-generator.component.html',
  styleUrls: ['./test-generator.component.scss']
})
export class TestGeneratorComponent implements OnInit {

  rolls: Array<string> = [];

  durationRolls: Array<string> = [];

  constructor() { }

  ngOnInit() {
    this.reroll();
  }

  reroll() {
    let table = TestGenTables.EXAMPLES;
    this.rolls = [];
    for(var entry of table) {
      var line = Gevaluator2Service.eval(entry[1], {
        "@tables": {
          EXAMPLES: table,
          CHAR_ATTR: TestGenTables.EX_CHAR_ATTR,
          DURATION: TestGenTables.DURATION
        }
      });
      this.rolls.push(line);
    }

    let durationTable = BasicTables.DURATION;
    this.durationRolls = [];
    for(var entry of durationTable) {
      this.durationRolls.push(RandomTablesService.eval(entry[1], {
        "@tables": BasicTables
      }));
    }
  }
}



