import { Injectable } from '@angular/core';

@Injectable()
export class GevaluatorService {

  public static CMD_REGEX = /\[\[([^\]]+)\]\]/g;
  public static G_ASSIGN_CMD_REGEX = /\[\[(\$?[a-z_]+\=[^\]]+)\]\]/g;
  public static MUTE_CMD_REGEX = /^\$(.+)$/;
  public static ROLL_CMD_REGEX = /^([0-9]+)?d([0-9]+)?$/;
  public static ASSIGN_CMD_REGEX = /^([a-z_]+)\=([^=]+)$/;
  public static COND_CMD_REGEX = /^([0-9a-z_]+)(\<|\>|!=|==)([0-9a-z_]+)\?([^:]+):(.+)?$/;
  public static OPERATOR_CMD_REGEX = /^([^\+\-\/\*\%\=]+)([\+\-\/\*\%])(.+)$/;

  constructor() { }

  static eval(sentence: string): string {
    var context = {};
    console.log("-------------")
    // Try to execute all assignments first
    var sentence = sentence.replace(this.G_ASSIGN_CMD_REGEX, (sub, cmd) => {
      return this.execCmd(cmd, context);
    });
    // Then all the rest
    var result = sentence.replace(this.CMD_REGEX, (sub, cmd) => {
      return this.execCmd(cmd, context);
    });

    console.log(context);
    return result;
  }

  static execCmd(cmd, context): string {
    if (this.MUTE_CMD_REGEX.test(cmd)) {
      var m = this.MUTE_CMD_REGEX.exec(cmd);
      this.execCmd(m[1], context);
      return "";
    } else if (this.ASSIGN_CMD_REGEX.test(cmd)) {

      var a = this.ASSIGN_CMD_REGEX.exec(cmd);
      return context[a[1]] = this.execCmd(a[2], context);

    } else if (this.COND_CMD_REGEX.test(cmd)) {


      return this.evaluateCondition(cmd, context);

    } else if (this.ROLL_CMD_REGEX.test(cmd)) {

      return this.roll(cmd);

    } else {
      return context[cmd] || cmd;
    }
  }

  static evaluateCondition(cmd, context): string {
    var match = this.COND_CMD_REGEX.exec(cmd);
    var A = this.execCmd(match[1], context);
    var B = this.execCmd(match[3], context);
    var op = match[2];
    return eval("" + A + op + B) ? this.execCmd(match[4], context) : this.execCmd(match[5], context);
  }

  static roll(rollCmd) {
    return rollCmd.replace(this.ROLL_CMD_REGEX, (sub, rolls, dieType) => {
      rolls = rolls ? rolls : 1;
      var result = 0;
      while (rolls > 0) {
        result += this.randInt(1, dieType);
        rolls--;
      }
      return result;
    });
  }

  static randInt(min, max): number {
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

  static evalInContext(scr, context) {
    // execute script in private context
    var r;
    try {
      r = (new Function("with(this) { return " + scr + "}")).call(context);
    } catch(e) {
      console.error(e);
    }
    return r;
  }

}



