import { Component, OnInit } from '@angular/core';
import { GearItem } from '../../data/Gear';
import { GearDataService } from '../../data/gear-data.service';

@Component({
  selector: 'app-equipment',
  templateUrl: './equipment.component.html',
  styleUrls: ['./equipment.component.scss']
})
export class EquipmentComponent implements OnInit {

  equipment: GearItem[] = [];
  weapons: GearItem[] = [];
  armor: GearItem[] = [];

  constructor(private gearData: GearDataService) { }

  ngOnInit() {
    this.equipment = this.gearData.findAll(g => g.tags.findIndex(t => t.name === "TAG.GEAR.EQUIPMENT_ITEM") > -1);
    this.armor = this.gearData.findAll(g => g.tags.findIndex(t => t.name === "TAG.GEAR.ARMOR_ITEM") > -1);
    this.weapons = this.gearData.findAll(g => g.tags.findIndex(t => t.name === "TAG.GEAR.WEAPON_ITEM") > -1);
  }

}



