import { Injectable } from '@angular/core';
import { TagDataService } from '../data/tag-data.service';
import { GearDataService } from '../data/gear-data.service';
import { Tag } from '../data/Tag';

@Injectable()
export class InitGearDataService {

  private _init: boolean = false;

  private coinTag: Tag;
  private weightTag: Tag;
  private piercingTag: Tag;
  private ammoTag: Tag;
  private damageTag: Tag;
  private usesTag: Tag;
  private armorTag: Tag;

  constructor(
    private tagDataService: TagDataService,
    private gearDataService: GearDataService) {
  }

  init(): void {
    if (!this._init) {
      this.coinTag = this.tagDataService.findOneByName("TAG.GEAR.COIN");
      this.weightTag = this.tagDataService.findOneByName("TAG.GEAR.WEIGHT");
      this.piercingTag = this.tagDataService.findOneByName("TAG.GEAR.PIERCING");
      this.ammoTag = this.tagDataService.findOneByName("TAG.GEAR.AMMO");
      this.damageTag = this.tagDataService.findOneByName("TAG.GEAR.DAMAGE");
      this.usesTag = this.tagDataService.findOneByName("TAG.GEAR.USES");
      this.armorTag = this.tagDataService.findOneByName("TAG.GEAR.ARMOR");
    }
  }

  _valueTag(value: string | number): Tag {
    return {
      name: "",
      domain: "",
      value: "" + value
    };
  }

  _weight(w: number): Tag {
    return Object.assign(this._valueTag(w), this.weightTag);
  }

  _coins(c: number): Tag {
    return Object.assign(this._valueTag(c), this.coinTag);
  }

  _piercing(p: number): Tag {
    return Object.assign(this._valueTag(p), this.piercingTag);
  }

  _ammo(a: number): Tag {
    return Object.assign(this._valueTag(a), this.ammoTag);
  }

  _damage(d: number): Tag {
    return Object.assign(this._valueTag(d), this.damageTag);
  }

  _uses(u: number): Tag {
    return Object.assign(this._valueTag(u), this.usesTag);
  }

  _armor(a: number): Tag {
    return Object.assign(this._valueTag(a), this.armorTag);
  }

  initEquipment() {
    this.init();
    let awkward = this.tagDataService.findOneByName("TAG.GEAR.AWKWARD");
    let dangerous = this.tagDataService.findOneByName("TAG.GEAR.DANGEROUS");
    let slow = this.tagDataService.findOneByName("TAG.GEAR.SLOW");
    let applied = this.tagDataService.findOneByName("TAG.GEAR.APPLIED");
    let touch = this.tagDataService.findOneByName("TAG.GEAR.TOUCH");
    let ration = this.tagDataService.findOneByName("TAG.GEAR.RATION");

    let equipmentItem = this.tagDataService.findOneByName("TAG.GEAR.EQUIPMENT_ITEM");

    this.gearDataService.insert({
      slug: "shield",
      name: "GEAR.ADV_EQP.NAME",
      uses: 5,
      description: "GEAR.ADV_EQP.DESC",
      tags: [
        equipmentItem,
        this._uses(5),
        this._weight(1),
        this._coins(20)
      ]
    });

  }

  initArmor() {
    this.init();
    let worn = this.tagDataService.findOneByName("TAG.GEAR.WORN");
    let armor = this.tagDataService.findOneByName("TAG.GEAR.ARMOR");
    let weight = this.tagDataService.findOneByName("TAG.GEAR.WEIGHT");
    let coin = this.tagDataService.findOneByName("TAG.GEAR.COIN");
    let clumsy = this.tagDataService.findOneByName("TAG.GEAR.CLUMSY");

    let armorItem = this.tagDataService.findOneByName("TAG.GEAR.ARMOR_ITEM");


    this.gearDataService.insert({
      slug: "armor_leather",
      name: "GEAR.ARMOR_LEATHER.NAME",
      tags: [
        armorItem,
        worn,
        this._armor(1),
        this._weight(1),
        this._coins(10)
      ]
    });

    this.gearDataService.insert({
      slug: "armor_chain",
      name: "GEAR.ARMOR_CHAIN.NAME",
      tags: [
        armorItem,
        worn,
        this._armor(1),
        this._weight(1),
        this._coins(10)
      ]
    });

    this.gearDataService.insert({
      slug: "armor_scale",
      name: "GEAR.ARMOR_SCALE.NAME",
      tags: [
        armorItem,
        worn,
        clumsy,
        this._armor(2),
        this._weight(3),
        this._coins(50)
      ]
    });

    this.gearDataService.insert({
      slug: "armor_plate",
      name: "GEAR.ARMOR_PLATE.NAME",
      tags: [
        armorItem,
        worn,
        clumsy,
        this._armor(3),
        this._weight(4),
        this._coins(350)
      ]
    });

    this.gearDataService.insert({
      slug: "shield",
      name: "GEAR.SHIELD.NAME",
      tags: [
        armorItem,
        this._armor(1),
        this._weight(2),
        this._coins(15)
      ]
    });

  }

  initWeapons() {
    this.init();
    let reachHand = this.tagDataService.findOneByName("TAG.GEAR.REACH_HAND");
    let reachClose = this.tagDataService.findOneByName("TAG.GEAR.REACH_CLOSE");
    let reachLong = this.tagDataService.findOneByName("TAG.GEAR.REACH_LONG");
    let reachNear = this.tagDataService.findOneByName("TAG.GEAR.REACH_NEAR");
    let reachFar = this.tagDataService.findOneByName("TAG.GEAR.REACH_FAR");
    let piercing = this.tagDataService.findOneByName("TAG.GEAR.PIERCING");
    let precise = this.tagDataService.findOneByName("TAG.GEAR.PRECISE");
    let messy = this.tagDataService.findOneByName("TAG.GEAR.MESSY");
    let forceful = this.tagDataService.findOneByName("TAG.GEAR.FORCEFUL");
    let awkward = this.tagDataService.findOneByName("TAG.GEAR.AWKWARD");
    let reload = this.tagDataService.findOneByName("TAG.GEAR.RELOAD");
    let twoHanded = this.tagDataService.findOneByName("TAG.GEAR.TWOHANDED");
    let thrown = this.tagDataService.findOneByName("TAG.GEAR.THROWN");

    let weaponItem = this.tagDataService.findOneByName("TAG.GEAR.WEAPON_ITEM");

    this.gearDataService.insert({
      slug: "bow_rugged",
      name: "GEAR.BOW_RUGGED.NAME",
      tags: [
        weaponItem,
        reachNear,
        this._coins(15),
        this._weight(2)
      ]
    });

    this.gearDataService.insert({
      slug: "bow_fine",
      name: "GEAR.BOW_FINE.NAME",
      tags: [
        weaponItem,
        reachNear,
        reachFar,
        this._coins(60),
        this._weight(2)
      ]
    });

    this.gearDataService.insert({
      slug: "bow_hunter",
      name: "GEAR.BOW_HUNTER.NAME",
      tags: [
        weaponItem,
        reachNear,
        reachFar,
        this._coins(100),
        this._weight(1)
      ]
    });

    this.gearDataService.insert({
      slug: "crossbow",
      name: "GEAR.CROSSBOW.NAME",
      tags: [
        weaponItem,
        reachNear,
        reload,
        this._coins(35),
        this._weight(3),
        this._piercing(1)
      ]
    });

    this.gearDataService.insert({
      slug: "arrows",
      name: "GEAR.ARROWS.NAME",
      tags: [
        weaponItem,
        this._ammo(3),
        this._coins(2),
        this._weight(1)
      ]
    });

    this.gearDataService.insert({
      slug: "arrows_elven",
      name: "GEAR.ARROWS_ELVEN.NAME",
      tags: [
        weaponItem,
        this._ammo(4),
        this._coins(20),
        this._weight(1)
      ]
    });

    this.gearDataService.insert({
      slug: "club",
      name: "GEAR.CLUB.NAME",
      tags: [
        weaponItem,
        reachClose,
        this._coins(1),
        this._weight(2)
      ]
    });

    this.gearDataService.insert({
      slug: "staff",
      name: "GEAR.STAFF.NAME",
      tags: [
        weaponItem,
        reachClose,
        twoHanded,
        this._coins(1),
        this._weight(1)
      ]
    });

    this.gearDataService.insert({
      slug: "dagger",
      name: "GEAR.DAGGER.NAME",
      tags: [
        weaponItem,
        reachHand,
        this._coins(2),
        this._weight(1)
      ]
    });

    this.gearDataService.insert({
      slug: "dagger_throwing",
      name: "GEAR.DAGGER_THROWING.NAME",
      tags: [
        weaponItem,
        thrown,
        reachNear,
        this._coins(2),
        this._weight(0)
      ]
    });

    this.gearDataService.insert({
      slug: "sword_short",
      name: "GEAR.SWORD_SHORT.NAME",
      tags: [
        weaponItem,
        reachClose,
        this._coins(8),
        this._weight(1)
      ]
    });

    this.gearDataService.insert({
      slug: "axe_hand",
      name: "GEAR.AXE_HAND.NAME",
      tags: [
        weaponItem,
        reachClose,
        this._coins(8),
        this._weight(1)
      ]
    });

    this.gearDataService.insert({
      slug: "mace",
      name: "GEAR.MACE.NAME",
      tags: [
        weaponItem,
        reachClose,
        this._coins(8),
        this._weight(1)
      ]
    });

    this.gearDataService.insert({
      slug: "spear",
      name: "GEAR.SPEAR.NAME",
      tags: [
        weaponItem,
        reachLong,
        thrown,
        reachNear,
        this._coins(5),
        this._weight(1)
      ]
    });

    this.gearDataService.insert({
      slug: "sword_long",
      name: "GEAR.SWORD_LONG.NAME",
      tags: [
        weaponItem,
        reachClose,
        this._damage(1),
        this._coins(15),
        this._weight(2)
      ]
    });

    this.gearDataService.insert({
      slug: "axe_battle",
      name: "GEAR.AXE_BATTLE.NAME",
      tags: [
        weaponItem,
        reachClose,
        this._damage(1),
        this._coins(15),
        this._weight(2)
      ]
    });

    this.gearDataService.insert({
      slug: "flail",
      name: "GEAR.FLAIL.NAME",
      tags: [
        weaponItem,
        reachClose,
        this._damage(1),
        this._coins(15),
        this._weight(2)
      ]
    });

    this.gearDataService.insert({
      slug: "halberd",
      name: "GEAR.HALBERD.NAME",
      tags: [
        weaponItem,
        reachLong,
        twoHanded,
        this._damage(1),
        this._coins(9),
        this._weight(2)
      ]
    });

    this.gearDataService.insert({
      slug: "rapier",
      name: "GEAR.RAPIER.NAME",
      tags: [
        weaponItem,
        reachClose,
        precise,
        this._coins(25),
        this._weight(1)
      ]
    });

    this.gearDataService.insert({
      slug: "estoc",
      name: "GEAR.ESTOC.NAME",
      tags: [
        weaponItem,
        reachClose,
        this._piercing(2),
        this._coins(50),
        this._weight(2)
      ]
    });

  }

}



