import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { GeneratorComponent } from './generator.component';
import { RandomTablesService } from './tables/random-tables.service';
import { BasicGeneratorsComponent } from './basic-generators/basic-generators.component';
import { SharedModule } from '../../shared/shared.module';
import { Routes, RouterModule } from '@angular/router';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    RouterModule
  ],
  declarations: [GeneratorComponent, BasicGeneratorsComponent],
  providers: [RandomTablesService]
})
export class GeneratorsModule { }



