import { Component, OnInit } from '@angular/core';
import { BasicTables } from '../tables/BasicTables.class';
import { RandomTablesService } from '../tables/random-tables.service';
import { QuotesTables } from '../tables/QuotesTables.class';
import { WildMagicTables } from '../tables/WildMagicTables.class';
import { HtHaDTables } from '../tables/HtHaDTables.class';
import { SpellNamesTables } from '../tables/SpellNameTables.class';

@Component({
  selector: 'app-basic-generators',
  templateUrl: './basic-generators.component.html',
  styleUrls: ['./basic-generators.component.scss']
})
export class BasicGeneratorsComponent implements OnInit {

  rolledDungeon: string;
  crypticQuote: string;
  wildMagicRolls: Array<string>;
  wildMagicRollsNumber: number = 1;
  rolledSpells: Array<string>;

  constructor() { }

  ngOnInit() {
    // this.rollDurations();
    this.rollWildMagic();
    this.rollSpells();
  }

  rollWildMagic() {
    this.wildMagicRolls = [];
    for (var i = 0; i < this.wildMagicRollsNumber; i++) {
      this.wildMagicRolls.push(RandomTablesService.eval(WildMagicTables._START, {
        "@tables": WildMagicTables
      }));
    }
  }

  rollDungeons() {
    this.rolledDungeon = RandomTablesService.eval(HtHaDTables._START, {
      "@tables": HtHaDTables
    });
  }

  rollSpells() {
    this.rolledSpells = [];

    for (var j = 0; j < SpellNamesTables.SB_SUITE.length; j++) {
      for (var i = 0; i < this.wildMagicRollsNumber; i++) {
        this.rolledSpells.push(RandomTablesService.eval(SpellNamesTables.SB_SUITE[j], {
          "@tables": SpellNamesTables
        }));
      }
    }

    // for (var i = 0; i < this.wildMagicRollsNumber; i++) {
    //   this.rolledSpells.push(RandomTablesService.eval(SpellNamesTables.RANDOMSPELL, {
    //     "@tables": SpellNamesTables
    //   }));
    // }
  }
}



