import { Component, OnInit } from '@angular/core';
import { Router, NavigationEnd, ActivatedRoute } from '@angular/router';
import { InitBasicMovesService } from './init-data/init-basic-moves.service';
import { TranslateService } from '@ngx-translate/core';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/mergeMap';
import { InitTagsService } from './init-data/init-tags.service';
import { InitSpecialMovesService } from './init-data/init-special-moves.service';
import { InitFighterClassService } from './init-data/init-fighter-class.service';
import { InitHeritageDataService } from './init-data/init-heritage-data.service';
import { InitGearDataService } from './init-data/init-gear-data.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

  constructor(
    private translate: TranslateService,
    private router: Router,
    private route: ActivatedRoute,
    private initTags: InitTagsService,
    private initGear: InitGearDataService,
    private initBasicMoves: InitBasicMovesService,
    private initSpecialMoves: InitSpecialMovesService,
    private initFighter: InitFighterClassService,
    private initHeritages: InitHeritageDataService) {

    this.translate.setDefaultLang('en');
    this.translate.use('it');
  }

  selectLanguage(lang: string) {
    console.log("Select", lang)
    this.translate.use(lang);
  }

  ngOnInit() {
    console.debug("App component - ngOnInit");

    this.initTags.initData();
    this.initGear.initEquipment();
    this.initGear.initArmor();
    this.initGear.initWeapons();
    this.initBasicMoves.initData();
    this.initSpecialMoves.initData();
    this.initHeritages.initData();
    this.initFighter.initData();

    this.router.events
      .filter((event) => event instanceof NavigationEnd)
      .subscribe((route: NavigationEnd) => {
        // let matches = route.url.match(/^\/([a-z]{2})\/.*/);
        // if(matches && matches.length > 1) {
        //   this.translate.use(matches[1]);
        // } else {
        //   this.translate.use(this.translate.getDefaultLang());
        // }
      });
  }
}



