import { Injectable } from '@angular/core';
import { TagDataService } from '../data/tag-data.service';
import { MoveDataService } from '../data/move-data.service';

@Injectable()
export class InitBasicMovesService {

  constructor(
    private tagData: TagDataService,
    private moveData: MoveDataService
  ) { }

  initData() {

    var basicMoveTag = this.tagData.findOneByName("TAG.BASEMOVE");
    var playerMoveTag = this.tagData.findOneByName("TAG.PLAYERMOVE");
    var unchainedTag = this.tagData.findOneByName("TAG.UNCHAINED");


    this.moveData.insert({
      tags: [playerMoveTag, basicMoveTag, unchainedTag],
      slug: "advantage",
      i18nPrefix: "MOVE.ADVANTAGE.",
      name: "NAME",
      trigger: "TRIGGER",
      effect: "EFFECT"
    });

    this.moveData.insert({
      tags: [playerMoveTag, basicMoveTag, unchainedTag],
      slug: "disadvantage",
      i18nPrefix: "MOVE.DISADVANTAGE.",
      name: "NAME",
      trigger: "TRIGGER",
      effect: "EFFECT"
    });

    this.moveData.insert({
      tags: [playerMoveTag, basicMoveTag],
      slug: "discern_realities",
      i18nPrefix: "MOVE.DISCERN_REALITIES.",
      name: "NAME",
      trigger: "TRIGGER",
      rollModifier: "CHARACTER.ATTRIBUTE.WIS",
      effect: "EFFECT",
      rollResultBreakdown: [
        {range: "10+", effect: "RES.10+"},
        {range: "7-9", effect: "RES.7-9"}
      ],
      details: "DETAILS",
      comments: [
        "COMMENT.1",
        "COMMENT.2",
        "COMMENT.3",
        "COMMENT.4"
      ]
    });

    this.moveData.insert({
      tags: [playerMoveTag, basicMoveTag],
      slug: "spout_lore",
      i18nPrefix: "MOVE.SPOUT_LORE.",
      name: "NAME",
      trigger: "TRIGGER",
      rollModifier: "CHARACTER.ATTRIBUTE.INT",
      rollResultBreakdown: [
        {range: "10+", effect: "RES.10+"},
        {range: "7-9", effect: "RES.7-9"}
      ],
      details: `DETAILS`,
      comments: [
        `COMMENT.1`,
        `COMMENT.2`
      ]
    });

    this.moveData.insert({
      tags: [playerMoveTag, basicMoveTag],
      slug: "defy_danger",
      i18nPrefix: "MOVE.DEFY_DANGER.",
      name: "NAME",
      trigger: "TRIGGER",
      effect: "EFFECT",
      rollResultBreakdown: [
        {range: "10+", effect: "RES.10+"},
        {range: "7-9", effect: "RES.7-9"}
      ],
      comments: [
        `COMMENT.1`
      ]
    });

    this.moveData.insert({
      tags: [playerMoveTag, basicMoveTag],
      slug: "aid_or_interfere",
      i18nPrefix: "MOVE.AID_OR_INTERFERE.",
      name: "NAME",
      trigger: "TRIGGER",
      rollModifier: "MOVE.AID_OR_INTERFERE.ROLL_MODIFIER",
      rollResultBreakdown: [
        {range: "10+", effect: "RES.10+"},
        {range: "7-9", effect: "RES.7-9"}
      ],
      comments: [
        `COMMENT.1`
      ]
    });

    this.moveData.insert({
      tags: [playerMoveTag, basicMoveTag, unchainedTag],
      slug: "assist",
      i18nPrefix: "MOVE.ASSIST.",
      name: "NAME",
      trigger: "TRIGGER",
      effect: "EFFECT",
      rollResultBreakdown: [
        {range: "10+", effect: "RES.10+"},
        {range: "7-9", effect: "RES.7-9"}
      ],
      details: "DETAILS",
      comments: [
        //`COMMENT.1`
      ]
    });

    this.moveData.insert({
      tags: [playerMoveTag, basicMoveTag],
      slug: "parley",
      i18nPrefix: "MOVE.PARLEY.",
      name: "NAME",
      trigger: "TRIGGER",
      rollModifier: "CHARACTER.ATTRIBUTE.CHA",
      effect: "EFFECT",
      rollResultBreakdown: [
        {range: "10+", effect: "RES.10+"},
        {range: "7-9", effect: "RES.7-9"}
      ],
      comments: [
        "COMMENT.1",
        "COMMENT.2"
      ]
    });

    this.moveData.insert({
      tags: [playerMoveTag, basicMoveTag],
      slug: "hack_and_slash",
      i18nPrefix: "MOVE.HACK_AND_SLASH.",
      name: "NAME",
      trigger: "TRIGGER",
      rollModifier: "CHARACTER.ATTRIBUTE.STR",
      rollResultBreakdown: [
        {range: "10+", effect: "RES.10+"},
        {range: "7-9", effect: "RES.7-9"}
      ],
      comments: [
        "COMMENT.1",
        "COMMENT.2",
        "COMMENT.3",
        "COMMENT.4"
      ]
    });

    this.moveData.insert({
      tags: [playerMoveTag, basicMoveTag],
      slug: "volley",
      i18nPrefix: "MOVE.VOLLEY.",
      name: "NAME",
      trigger: "TRIGGER",
      rollModifier: "CHARACTER.ATTRIBUTE.DEX",
      rollResultBreakdown: [
        {range: "10+", effect: "RES.10+"},
        {range: "7-9", effect: "RES.7-9"}
      ],
      details: "DETAILS",
      comments: [
        "COMMENT.1",
        "COMMENT.2"
      ]
    });

    this.moveData.insert({
      tags: [playerMoveTag, basicMoveTag],
      slug: "defend",
      i18nPrefix: "MOVE.DEFEND.",
      name: "NAME",
      trigger: "TRIGGER",
      rollModifier: "CHARACTER.ATTRIBUTE.CON",
      rollResultBreakdown: [
        {range: "10+", effect: "RES.10+"},
        {range: "7-9", effect: "RES.7-9"}
      ],
      details: "DETAILS",
      comments: [
        "COMMENT.1",
        "COMMENT.2"
      ]
    });

  }

}



