import { Injectable } from '@angular/core';
import { CharacterHeritage } from './CharacterHeritage';
import { ObjectCache } from './ObjectCache';

@Injectable()
export class HeritageDataService extends ObjectCache<CharacterHeritage> {

  constructor() {
    super();
  }

  findByName(name:string): CharacterHeritage[] {
    return this.findAll(h => h.name == name);
  }

  searchByName(namePartial:string): CharacterHeritage[] {
    return this.findAll(h => h.name.indexOf(namePartial) > -1);
  }
}



