import { Component, OnInit, Input } from '@angular/core';
import { GearItem, GearStack } from '../../data/Gear';
import { Tag } from '../../data/Tag';

@Component({
  selector: 'gear-stack-component',
  templateUrl: './gear.component.html',
  styleUrls: ['./gear.component.scss']
})
export class GearStackComponent implements OnInit {

  @Input("stack")
  stack: GearStack;
  @Input("item")
  item: GearItem;
  tags: Tag[];

  @Input("showTags")
  showTags: boolean = true;

  constructor() { }

  ngOnInit() {
    if(!this.stack) {
      this.stack = {
        item: this.item,
        size: 1
      }
    };
    this.tags = this.stack.item.tags.filter(t => t.domain === 'GEAR');
  }

}



