import { Injectable } from '@angular/core';
import { ObjectCache } from './ObjectCache';
import { Move } from './Move';



@Injectable()
export class MoveDataService extends ObjectCache<Move> {

  constructor() {
    super();
  }

  findOneBySlug(slug:string): Move {
    let result = this.findAll(move => move.slug == slug)
    return result.length ? result[0] : null;
  }

  findBySlug(slug:string): Move[] {
    return this.findAll(move => move.slug.indexOf(slug) == 0);
  }

  findByName(name:string): Move[] {
    return this.findAll(move => move.name == name);
  }
}



