import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MasterComponent } from './master.component';
import { GeneratorComponent } from './generators/generator.component';
import { BasicGeneratorsComponent } from './generators/basic-generators/basic-generators.component';
import { TestGeneratorComponent } from './test-generator/test-generator.component';

const routes: Routes = [{
  path: 'master',
    component: MasterComponent,
    children: [
      {
        path: 'generators',
        component: GeneratorComponent,
        children: [
          {
            path: 'test',
            component: TestGeneratorComponent
          },
          {
            path: 'basic',
            component: BasicGeneratorsComponent
          }
        ]
      }
    ]
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MasterRoutingModule { }



