import { Component, OnInit } from '@angular/core';
import { HeritageDataService } from '../../data/heritage-data.service';
import { CharacterHeritage } from '../../data/CharacterHeritage';

@Component({
  selector: 'app-heritages',
  templateUrl: './heritages.component.html',
  styleUrls: ['./heritages.component.css']
})
export class HeritagesComponent implements OnInit {

  heritages: CharacterHeritage[] = [];

  constructor(private heritageData: HeritageDataService) { }

  ngOnInit() {
    this.heritages = this.heritageData.findAll(h => true);
  }

}



