import { Injectable } from '@angular/core';
import { GearItem } from './Gear';
import { ObjectCache } from './ObjectCache';

@Injectable()
export class GearDataService extends ObjectCache<GearItem> {

  constructor() {
    super();
  }

  searchByName(namePartial: string): GearItem[] {
    return this.findAll(item => item.name.indexOf(namePartial) > -1);
  }

  findOneBySlug(slug: string): GearItem {
    let found = this.findAll(item => item.slug === slug);
    return found.length ? found[0] : null;
  }

}



