import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MoveDetailComponent } from './move-detail/move-detail.component';
import { TranslateModule } from '@ngx-translate/core';
import { RouterModule } from '@angular/router';
import { ShowCharacterComponent } from './show-character/show-character.component';
import { HeritageComponent } from './heritage/heritage.component';
import { PlaybookCardComponent } from './playbook-card/playbook-card.component';
import { GearStackComponent } from './gear-stack/gear.component';
import { TagComponent } from './tag/tag.component';
import { FormsModule } from '@angular/forms';

@NgModule({
  imports: [
    CommonModule,
    TranslateModule,
    RouterModule.forChild([]),
    FormsModule
  ],
  declarations: [MoveDetailComponent, ShowCharacterComponent, HeritageComponent, PlaybookCardComponent, GearStackComponent, TagComponent],
  exports: [
    MoveDetailComponent,
    TranslateModule,
    FormsModule,
    ShowCharacterComponent,
    HeritageComponent,
    PlaybookCardComponent,
    GearStackComponent,
    TagComponent
  ]
})
export class SharedModule { }



